
import dayjs from "dayjs";
import Dict from "@/components/dict";
import ClientLink from "@/components/client/client_link";
import { mapActions, mapState, mapGetters } from "vuex";
import { backMixin } from "../../common/js/picBack";
import XHeader from 'vux/src/components/x-header/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'

export default {
mixins: [backMixin],
name: "newCost",
components: {
XHeader,
Group,
Radio,
Cell,
XInput,
Datetime,
PopupRadio,
PopupPicker,
XTextarea,
XButton,
Loading,
Toast,
Dict,
ClientLink,
Checker,
CheckerItem,
Actionsheet,
Previewer,
XImg
},
data() {
return {
formData: {
ID: 0,
CostType: "收入",
OrderID: 0,
CostDate: "",
CustomerID: 0,
CustomerName: "",
LinkUserID: "",
LinkUserName: "",
Description: "",
CostClassID: "",
CostTypeName: "",
Remark: "",
CostMoney: "",
ReturnID: 0,
ImgAttach: []
},
old: { CostMoney: "", Description: "" },
change: false,
changeForm: [],
costFormCostDate: dayjs(new Date()).format("YYYY-MM-DD"),
fId: "", //修改时传来该条记录的id
cId: "", //从我的客户调用此页面时传来的客户id
cLink: "", //从我的客户调用此页面时传来的联系人id
isSelectCust: true, //是否可以选择客户(从我的客户选择客户后新建收支时不可选择)
costTitle: "新建报销",
docmHeight: document.documentElement.clientHeight, // 默认屏幕高度
showHeight: document.documentElement.clientHeight, // 实时屏幕高度
submitShow: true, //页面高度变化时（显示虚拟键盘）,隐藏提交按钮
ifSave: false, //识别离开此页面时是否提交了表单数据
historyTypeList: [], //最近选择的费用类型
clickNum: 0,
userID: 0, //用户id，读写localstorage
typeName: "",
quickSelect: {},
costTypeID: "",
changeFlag: false,

options: {
saveImg(el) {
console.log(el);
}
},
showAction: false,
actionMenu: {
shot: "拍照",
album: "从相册选择"
},
pics: []
};
},
methods: {
...mapActions(["initDictPromise"]),
goBack() {




window.history.go(-1);

},
inputCostMoney(value) {
let _value = Number(value);
if (_value < 0) {
_value = 0;
}
this.formData.CostMoney = _value.toFixed(2);
if (_value > 10000000000) {
this.$vux.toast.show({
text: "报销金额必须大于0小于10000000000",
position: "middle",
type: "warn"
});

}
},
ifClear(value) {
let _value = Number(value);
if (_value == 0.0) {
this.formData.CostMoney = "";
}
},
save() {
let cust = this.$refs.client.getData();

if (
this.formData.CostMoney <= 0 ||
this.formData.CostMoney >= 10000000000
) {
this.$vux.toast.show({
text: "报销金额必须大于0小于10000000000",
position: "middle",
type: "warn"
});
return;
}
if (!this.costFormCostDate) {
this.$vux.toast.show({
text: "请选择报销日期",
position: "middle",
type: "warn"
});
return;
}
let saveObj = Object.assign({}, this.formData);
if (cust.CustomerID) {
saveObj.CustomerID = cust.CustomerID;
saveObj.LinkUserID = cust.LinkUserID;
}
saveObj.CostClassID = this.costTypeID || this.quickSelect;
if (typeof saveObj.CostClassID !== "number") {
saveObj.CostClassID = null;
}
saveObj.CostType = 2;
saveObj.CostDate = dayjs(this.costFormCostDate).format(
"YYYY-MM-DD HH:mm:ss"
);
saveObj.IsExpense = 1
let namelist = this.$store.state.dict["22"];
let obj = namelist.find(item => {
if (item.ID == saveObj.CostClassID) {
return item;
}
});
if (obj) {
this.typeName = obj.DictName;
}
let costType = {
ID: saveObj.CostClassID,
name: this.typeName
};

this.$vux.loading.show({
text: "提交中..."
});
this.postAxios("/Cost/SaveCrmCostRecord", saveObj).then(res => {
this.$vux.toast.show({
type: "success",
text: "保存成功"
});
this.$bus.emit("onSave", true);
this.$bus.emit("refrechCostList", true);
setTimeout(() => {
this.$vux.toast.hide();
this.$vux.loading.hide();
this.ifSave = true;
if (costType.ID) {
this.selectType(costType);
}
this.$route.meta.isBack = true;
this.$router.back();
}, 500);
});
},
getData() {
if (this.fId) {
this.costTitle = "修改报销";
this.getAxios(`/Cost/GetCrmCostRecordByID/${this.fId}`).then(res => {
this.formData = { ...res.Data };
if (this.formData.CostDate) {
this.costFormCostDate = dayjs(this.formData.CostDate).format(
"YYYY-MM-DD"
);
}
this.formData.CostType =
this.formData.CostType == 1 ? "收入" : "支出";
this.old.CostMoney = this.formData.CostMoney;
this.old.Description = this.formData.Description;
this.costTypeID = this.formData.CostClassID;
this.formData.ImgAttach.forEach(item => {
item.PicPath = `${api50}/api/v1/File/DownLoadPic?filePath=${
item.FilePath
}`;
this.pics.push(item);
});
});
}
},
goto(path, id) {
this.$router.push({
path,
query: {
id
}
});
},
async selectType(item) {
if (this.clickNum > 0) return false;
this.clickNum += 1;
let cache;

try {
cache = localStorage.getItem("costType");
} catch (err) {
cache = sessionStorage.getItem("costType");
}
let dict = cache ? JSON.parse(cache) : {},
list = cache ? JSON.parse(cache)[this.userID] || [] : [],
index = -1;

list.forEach((data, indexs) => {
if (data.ID === item.ID) {
index = indexs;
}
});
if (index >= 0) {
list.splice(index, 1);





}
list.unshift(item);
list = list.slice(0, 5);

dict[this.userID] = list;
try {
localStorage.setItem("costType", JSON.stringify(dict));
} catch (err) {
sessionStorage.setItem("costType", JSON.stringify(dict));
}
},

getCacheClient() {

let cache;
try {
cache = localStorage.getItem("costType");
} catch (err) {
cache = sessionStorage.getItem("costType");
}
let list = cache ? JSON.parse(cache)[this.userID] || [] : [];
this.historyTypeList = [...list];
},
cType(name) {
this.typeName = name;
},

startSelectPic() {
this.showAction = true;
},
chooseSelectType(tid) {
if (this.maxPic <= 0) {
this.$vux.toast.show({
text: "最多可选9张图片",
position: "middle",
type: "cancel"
});
return;
}

if (tid == "shot") {
this.shotAndChoose();
} else if (tid == "album") {
this.addPic();
} else {
}
},
shotAndChoose() {
let map = document.querySelector(".checkPage");
if (this.maxPic <= 0) {
this.$vux.toast.show({
text: "最多可选9张图片",
position: "middle",
type: "cancel"
});
return;
}
const camera = plus.camera.getCamera();
camera.captureImage(path => {
if (path) {
this.selectPic([path]);
}
});
},
addPic() {



let _this = this;
if (this.maxPic <= 0) {
this.$vux.toast.show({
text: "最多可选9张图片",
position: "middle",
type: "cancel"
});
return;
}
plus.gallery.pick(
function(pics) {
_this.selectPic(pics.files);
},
function(e) {
console.log("取消选择图片");
},
{
filter: "image",
multiple: true,
maximum: _this.maxPic,
system: false
}
);
},
selectPic(pics) {
this.uploadPic(pics);
},
uploadPic(pics) {
let _this = this;
let _server = this.BillFileUrl(1);
let wt = plus.nativeUI.showWaiting();
wt.close();
let task = plus.uploader.createUpload(
_server,
{ method: "POST" },
function(t, status) {

if (status == 200) {
_this.$vux.loading.hide();
wt.close();
let res = JSON.parse(t.responseText);

if (res.State == 1) {
res.Data.forEach(item => {
item.PicPath = `${api50}/api/v1/File/DownLoadPIc?filePath=${
item.FilePath
}`;
_this.pics.push({ src: item.PicPath });
});
} else {
_this.$vux.toast.show({
text: res.ErrorMessage,
position: "middle",
type: "cancel"
});
return false;
}
res.Data.map(item => {
item.FileSize = 100;
item.FileType = 1;
});
_this.formData.ImgAttach.push(...res.Data);
} else {
alert("上传失败！");
}
}
);
let _promiseList = [];
for (let i = 0; i < pics.length; i++) {
let pic = pics[i];
_promiseList.push(this.zipPic(pic, i));
}

let index = 0;
task.addEventListener(
"statechanged",
(upload, status) => {
if (upload.totalSize > 1024 * 1024 * 10) {
this.$vux.toast.show({
type: "warn",
text: "图片大小不超过10M"
});
task.abort();
return false;
} else {
index++;
}
},
false
);

Promise.all(_promiseList)
.then(images => {
for (let i = 0; i < images.length; i++) {
task.addFile(images[i].path, { key: images[i].name });
}
})
.then(() => {
task.start();
});
},
zipPic(pic, idx) {
return new Promise((resolve, reject) => {
let reg = new RegExp("(.{" + pic.lastIndexOf(".") + "})");
var zipFile = pic.replace(reg, "$1_zip");
var zipExt = zipFile.substr(
zipFile.lastIndexOf(".") + 1,
zipFile.length - zipFile.lastIndexOf(".")
);
var zipPath =
zipFile.substr(0, zipFile.lastIndexOf("/") + 1) + idx + "." + zipExt;
let fileName = zipPath.substr(
zipPath.lastIndexOf("/") + 1,
zipPath.length - zipPath.lastIndexOf("/")
);
plus.zip.compressImage(
{
src: pic,
dst: zipPath,
overwrite: true,
width: "50%",
quality: 60
},
function(e) {
resolve({ path: e.target, name: fileName });
},
function(e) {
reject(e);
}
);
});
},
removePic(idx) {
this.formData.ImgAttach.splice(idx, 1);
this.pics.splice(idx, 1);
}
},
watch: {
showHeight: function() {
if (this.docmHeight > this.showHeight) {
this.submitShow = false;
} else {
this.submitShow = true;
}
},
quickSelect(newvalue, oldvalue) {
if (this.changeFlag) {
this.costTypeID = "";
}
this.changeFlag = !this.changeFlag;
},
costTypeID() {
if (this.changeFlag) {
this.quickSelect = "";
}
this.changeFlag = !this.changeFlag;
}
},
computed: {
...mapState(["dict"]),
maxPic() {
return 9 - this.formData.ImgAttach.length;
}
},
beforeRouteLeave(to, from, next) {
let ifChange =
this.old.CostMoney != this.formData.CostMoney ||
this.old.Description != this.formData.Description;
if (!this.ifSave) {
if (ifChange) {
this.$vux.confirm.show({
title: "继续退出?",
content: "您已编辑过，退出将丢失这些内容!",
onCancel() {
next(false);
},
onConfirm() {
next();
}
});
} else {
next();
}
} else {
to.meta.keepAlive = false;
next();
}
},
mounted() {
let user = JSON.parse(localStorage.getItem("user"));
this.userID = user ? user.data.UserID : 0;
this.getCacheClient();
let query = this.$route.query;
this.fId = query.id;
if (query.cId) {
this.isSelectCust = false;
this.cId = query.cId;
this.cLink = query.cLink;
this.formData.CustomerID = this.cId;
this.formData.CustomerName = query.cName;
this.formData.LinkUserID = this.cLink;
}

this.getData();


window.onresize = () => {
return (() => {
this.showHeight = document.body.clientHeight;
})();
};
}
};



